	module.exports = {"data":{"markdownRemark":{"html":"<p>今回は仕事の方で時間とられてしまい、時封城リメイクの体験版公開は無理に近い状態です。大変申し訳ないのですが、今回は既刊のみとさせていただきます。 その代りと言ってはなんですが、ウチが体験版Flash制作担当した某パズルゲームがNINTENDO DSで今日発売されたようなので、良かったらプレイしてやってくださいな。</p>\n<h2>障害情報</h2>\n<p>danmaqサイト移転はほぼ完了いたしましたが、まだthGパッチサーバやweb拍手もどきなど一部コンテンツに不具合が生じております。ちょっと年明けまで手直しする時間が取れそうにないので、もう暫くの間お待ちください。</p>\n<h2>近況</h2>\n<p>iPhoneはユーザに対して厳しいとよく言われるが、開発者に対しては<em>もっと</em>厳しいってことが痛いほどよく分かった。NDAが緩和されて間もないせいかネットにプログラム関連の情報が殆どない。まぁあと1か月もすればだいぶマシになってくるんだろうけど、そもそもObjective-Cが俺の肌に合わない(それはただの言い訳。Javaも昔は大嫌いだったし)腹いせに今作ってるやつへ白雪姫が倒れるアニメのイースターエッグでも埋め込んでやろうか。</p>","frontmatter":{"cover":null,"date":"2008-12-18T00:14:00+09:00","strDate":"2008/12/17","title":"ごめんむりぽ"}}},"pathContext":{"path":"/ja/blog/2008/12/18-ごめんむりぽ/","langKey":"ja"}}

